import React from "react";
import {
  GiDramaMasks,
  GiPointyHat,
  GiPerspectiveDiceSixFacesRandom
} from "react-icons/gi";
import { NavLink } from "react-router-dom";
import { FaArrowRight } from "react-icons/fa";
import "./Genres.scss";
// hardcoded part with most popular genres
// TODO make a component that holds single genre
const Genres = () => {
  return (
    <div className="genres">
      <div className="genre-section">
        <div className="genre-title animated fade-in-left">DRAMA</div>
        <GiDramaMasks className="genre-icon animated fade-in-left" />
        <div className="genre-description animated fade-in-left">
          Drama books are full of riveting conflict and dialogue. This is one of
          the oldest forms of storytelling from the ancient world.
        </div>
        <NavLink
          to="/books/drama"
          className="genre-button animated-delay fade-in-left"
        >
          All dramas&nbsp;&nbsp;
          <FaArrowRight className="arrow" />
        </NavLink>
      </div>
      <div className="genre-section animated fade-in-left">
        <div className="genre-title animated fade-in-left">FANTASY</div>
        <GiPointyHat className="genre-icon animated fade-in-left" />
        <div className="genre-description animated fade-in-left">
          Fantasy is a genre of speculative fiction set in a fictional universe,
          often inspired by real world myth and folklore.
        </div>
        <NavLink
          to="/books/fantasy"
          className="genre-button animated-delay fade-in-left"
        >
          All fantasies&nbsp;&nbsp;
          <FaArrowRight className="arrow" />
        </NavLink>
      </div>
      <div className="genre-section animated fade-in-left">
        <div className="genre-title animated fade-in-left">MYSTERY</div>
        <GiPerspectiveDiceSixFacesRandom className="genre-icon animated fade-in-left" />
        <div className="genre-description animated fade-in-left">
          Mysteries are book whose stories focus on a puzzling crime, situation,
          or circumstance that needs to be solved.
        </div>
        <NavLink
          to="/books/mystery"
          className="genre-button animated-delay fade-in-left"
        >
          All mysteries&nbsp;&nbsp;
          <FaArrowRight className="arrow" />
        </NavLink>
      </div>
    </div>
  );
};

export default Genres;
