import React from "react";
import "./Header.scss";
import { FaArrowRight } from "react-icons/fa";
import { NavLink } from "react-router-dom";
import bgImg from "../../../assets/whitebooks.jpg";

const Header = () => {
  return (
    <div className="header ">
      <img className="header-image" src={bgImg} alt="bgImg" />
      <div className="quote fade-in-left animated">
        <div className="quote-text">
          “The more that you read, the more things you will know. The more that
          you learn, the more places you’ll go.”
          <br />
          <br />– Dr Seuss
        </div>
        <NavLink
          to="/books/all"
          className="quote-button fade-in-left animated-delay"
        >
          Expand your knowledge here <FaArrowRight className="arrow" />
        </NavLink>
      </div>
    </div>
  );
};

export default Header;
