import React from "react";
import "./Footer.scss";
import { FaMapMarkerAlt } from "react-icons/fa";
import Logo from "../../../assets/Logo.png";

const Footer = () => {
  return (
    <div className="footer">
      <div className="footer-logo">
        <img src={Logo} alt="footerlogo" className="footer-logo-img" />
      </div>

      <div className="footer-about animated fade-in-left">
        <h1>About:</h1>
        <h2>
          Hoot is a customer-focused store, designed to spur discovery, a place
          where customers can find great books and products, and learn more
          about bookss. To give you more information as you browse, we’ve
          created unique features to highlight what people are reading, how they
          are reading and what they are loving, to help customers discover great
          new books.
        </h2>
      </div>
      <div className="footer-location animated fade-in-left">
        <h1>Where to find us:</h1>
        <h2>
          <FaMapMarkerAlt className="footer-pin" /> Stonechat Road 25
        </h2>
        <h2>
          <FaMapMarkerAlt className="footer-pin" /> Nelson Brook 371
        </h2>
        <h2>
          <FaMapMarkerAlt className="footer-pin" /> Hartland Passage 14
        </h2>
      </div>
    </div>
  );
};

export default Footer;
