import React, { useState, useEffect } from "react";
import { useParams } from "react-router";
import bookImage from "../../assets/readingBook.svg";
import allBooksData from "../../assets/tmpDataBooks.json";
import Pagination from "./Pagination";
import Posts from "./Posts";
import "./Books.scss";
import "./Elements.scss";
// component that holds all the filters and books
const Books = () => {
  // params are use to determine genre or author of a book
  const params = useParams();
  // initial filter values
  const [selectedGenre, setSelectedGenre] = useState(
    params.genre ? params.genre : "all"
  );
  // if genre params change,filter changes
  useEffect(() => {
    setSelectedGenre(params.genre);
  }, [params.genre]);

  const [authorValue, setAuthorValue] = useState(
    params.author ? params.author : ""
  );
  // if auther params change,filters change
  useEffect(() => {
    if (params.author) setSelectedGenre("all");
  }, [params.author]);
  const [searchValue, setSearchValue] = useState("");
  const [priceValue, setPriceValue] = useState(100);
  // books to show after all the books have been filtered,initially all
  const [filteredBooks, setFilteredBooks] = useState(allBooksData);
  // pagination
  const [posts, setPosts] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(12);
  // items to show
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = posts.slice(indexOfFirstPost, indexOfLastPost);
  // page change function
  const paginate = pageNumber => setCurrentPage(pageNumber);
  // set posts
  useEffect(() => {
    setPosts(filteredBooks);
  }, [filteredBooks]);
  // set page to 1 when a filter changes
  useEffect(() => {
    setCurrentPage(1);
  }, [posts]);
  // filters applying
  useEffect(() => {
    let byPriceGenre;
    if (selectedGenre === "all")
      byPriceGenre = allBooksData.filter(book => book.price < priceValue);
    else
      byPriceGenre = allBooksData.filter(
        book => book.genre === selectedGenre && book.price < priceValue
      );
    const byTitle = byPriceGenre.filter(book => {
      if (book.title.toLowerCase().match(searchValue.toLowerCase()) !== null)
        return true;
      return false;
    });
    const byAuthor = byTitle.filter(book => {
      if (book.author.toLowerCase().match(authorValue.toLowerCase()) !== null)
        return true;
      return false;
    });
    const afterAllFilters = byAuthor;
    setFilteredBooks(afterAllFilters);
  }, [selectedGenre, authorValue, priceValue, searchValue]);
  // // scrolls to top of page for better UX
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="books">
      <img src={bookImage} alt="book" className="corner-image" />
      <div className="filters animated fade-in">
        <div className="books-top">
          <div>
            <h1>Select genre:</h1>
            <select
              value={selectedGenre}
              name="genres"
              className="select-genre"
              onChange={e => setSelectedGenre(e.target.value)}
            >
              <option value="all">All</option>
              <option value="action">Action and Adventure</option>
              <option value="crime">Crime</option>
              <option value="drama">Drama</option>
              <option value="fantasy">Fantasy</option>
              <option value="mystery">Mystery</option>
              <option value="romance">Romance</option>
              <option value="thriller">Thriller</option>
              <option value="other">Other</option>
            </select>
          </div>

          <div className="range">
            <h1>Price : 0 - {priceValue}&euro;</h1>
            <input
              type="range"
              min="1"
              max="100"
              defaultValue="100"
              onChange={e => setPriceValue(e.target.value)}
            />
          </div>
        </div>
        <div className="books-searches">
          <h1>Search by name:</h1>
          <input
            className="input-search"
            type="text"
            placeholder=" Search..."
            onChange={e => setSearchValue(e.target.value)}
          />
          <h1>Search by author:</h1>
          <input
            className="input-author"
            type="text"
            placeholder=" Author..."
            value={authorValue}
            onChange={e => setAuthorValue(e.target.value)}
          />
        </div>
      </div>
      {filteredBooks.length > 0 ? (
        <div>
          <Posts
            posts={currentPosts}
            className="posts-container animated-delay fade-in"
          />
          <Pagination
            postsPerPage={postsPerPage}
            totalPosts={posts.length}
            paginate={paginate}
            selectedPage={currentPage}
          />
        </div>
      ) : (
        <p className="no-match">No books match your filters</p>
      )}
    </div>
  );
};

export default Books;
